import { useEffect, useState } from "react";
import { Pocket } from "react-feather";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  Alert,
  Card,
  CardBody,
  Col,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import { getCertificate } from "../store/action";
import CertificateTab from "./Certificate";

const CertificateEdit = () => {
  const [activeTab, setActiveTab] = useState("1"),
    store = useSelector((state) => state.certificate),
    dispatch = useDispatch(),
    { id } = useParams();
  const toggle = (tab) => setActiveTab(tab);

  useEffect(() => {
    (async () => {
      dispatch(await getCertificate(parseInt(id)));
    })();
  }, [dispatch, id]);

  return store?.selected !== null && store?.selected !== undefined ? (
    <Row className="app-user-edit">
      <Col sm="12">
        <Card>
          <CardBody className="pt-2">
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === "1"} onClick={() => toggle("1")}>
                  <Pocket size={14} />
                  <span className="align-middle d-none d-sm-block">
                    <FormattedMessage id="Certificate" />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="1">
                <CertificateTab selected={store.selected} />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  ) : (
    <Alert color="danger">
      <h4 className="alert-heading">Not found</h4>
    </Alert>
  );
};
export default CertificateEdit;
