import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";

import "react-phone-number-input/style.css";

import { isObjEmpty } from "@utils";

import classnames from "classnames";
import { Controller, useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "@constants/validate";
import { getCertificate, updateCertificate } from "../store/action";
import classNames from "classnames";
import Select from "react-select";
import draftToHtml from "draftjs-to-html";
import { convertToRaw, EditorState } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import avatarBlank from "./../../../../../assets/images/avatars/bg-blank.png";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";
import { htmlToDraftUtil } from "@utils";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { getProduct as getDataProduct } from "../store/action";

const CertificateTab = ({ selected, intl }) => {
  const store = useSelector((state) => state.certificate);
  const cate = yup.object({
    title: yup
      .string()
      .required(<FormattedMessage id="The title field is required" />),
    provider: yup
      .string()
      .required(<FormattedMessage id="Provider field is required" />),
    position: yup
      .string()
      .required(<FormattedMessage id="Position field is required" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });
  const history = useHistory();
  const [CertificateData, setCertificateData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const [fileImage, setFileImage] = useState();
  const [avatar, setAvatar] = useState(avatarBlank);
  const [status, setStatus] = useState(null);
  const [type, setType] = useState(null);
  const [disable, setDisable] = useState(false);
  const { id } = useParams();
  const EditCertificateOptions = validateOptions.EditCertificateOptions;
  const { product } = useSelector((state) => state.certificate);
  const [selectProduct, setSelectProduct] = useState(null);
  const [valueContent, setValueContent] = useState(EditorState.createEmpty());
  useEffect(() => {
    if (selected) {
      setValueContent(htmlToDraftUtil(selected?.content || " "));
    }
  }, [selected]);

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };

  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          lang,
          product_type: 0,
        },
        // skip: 0,
        // limit: 1000,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang]);

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        CertificateData !== null &&
        selected.id !== CertificateData.id)
    ) {
      setCertificateData(selected);
    }
  }, [selected, CertificateData]);
  useEffect(() => {
    if (store.status === 200) {
      history.push("/apps/course/certificate/list");
    }
  }, [store.status, history]);

  const onSubmit = async (values) => {
    if (isObjEmpty(errors)) {
      const content =
        (valueContent &&
          draftToHtml(convertToRaw(valueContent?.getCurrentContent()))) ||
        null;

      setDisable(true);
      var urlImage = " ";
      if (avatar) {
        const newData = avatar.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: fileImage.type.split("/")[1],
          };
          await uploadImage(data, fileImage).then((res) => {
            urlImage = res?.data;
          });
        } else {
          urlImage = avatar;
        }
        if (urlImage) {
          dispatch(
            updateCertificate({
              id: parseInt(id),
              data: {
                ...values,
                title: values?.title || "",
                provider: values?.provider || "",
                position: values?.position || "",
                productId: parseInt(selectProduct?.value),
                content: content || "",
                image: urlImage,
                status: parseInt(values.status),
                type: String(values?.type),
                lang,
              },
            })
          );
        }
      }
    }
  };
  useEffect(() => {
    if (CertificateData) {
      setAvatar(CertificateData?.image);
      setStatus(CertificateData.status);
      setType(CertificateData.type);
      setSelectProduct({
        value: CertificateData?.product?.id,
        label: CertificateData?.product?.product_names[0]?.name,
      });
    }
  }, [CertificateData, CertificateData?.product?.id]);
  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.name} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="certificate_title">
              <FormattedMessage id="certificate_title" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="title"
              id="title"
              innerRef={register(EditCertificateOptions.title)}
              onBlur={() => {
                let title = document.getElementById("title");
                if (title && title.value) {
                  title.value = title.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["title"] })}
              defaultValue={CertificateData && CertificateData.title}
            />
            <small className="text-danger">
              {errors?.title && errors.title.message}
            </small>
            {errors?.title?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="Content">
              <FormattedMessage id="Content" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Editor
              stripPastedStyles={true}
              editorState={valueContent}
              onEditorStateChange={(data) => setValueContent(data)}
              name="content"
              innerRef={register}
            />
          </FormGroup>
          <FormGroup>
            <Label for="certificate_type">
              <FormattedMessage id="certificate_type" />
            </Label>

            <Input
              type="select"
              name="type"
              value={type}
              onChange={(e) => setType(e.target.value)}
              id="type"
              innerRef={register({ required: true })}
            >
              <option value="0">
                {intl.formatMessage({ id: "Professional" })}
              </option>
              <option value="1">
                {intl.formatMessage({ id: "Advantage" })}
              </option>
              <option value="2">{intl.formatMessage({ id: "Basic" })}</option>
              <option value="3">{intl.formatMessage({ id: "Pratice" })}</option>
            </Input>
          </FormGroup>
          <FormGroup>
            <Label for="Provider">
              <FormattedMessage id="Provider" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="provider"
              id="provider"
              innerRef={register(EditCertificateOptions.provider)}
              onBlur={() => {
                let provider = document.getElementById("provider");
                if (provider && provider.value) {
                  provider.value = provider.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["provider"] })}
              defaultValue={CertificateData && CertificateData.provider}
            />
            <small className="text-danger">
              {errors?.provider && errors.provider.message}
            </small>
            {errors?.provider?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="position">
              <FormattedMessage id="position" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="position"
              id="position"
              innerRef={register(EditCertificateOptions.position)}
              onBlur={() => {
                let position = document.getElementById("position");
                if (position && position.value) {
                  position.value = position.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["position"] })}
              defaultValue={CertificateData && CertificateData.position}
            />
            <small className="text-danger">
              {errors?.position && errors.position.message}
            </small>
            {errors?.position?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label>
              <FormattedMessage id="Course" />
              <span className="text-danger"> * </span>
            </Label>
            {selectProduct && (
              <Controller
                control={control}
                name="productId"
                render={({ field }) => {
                  return (
                    <Select
                      id="productId"
                      innerRef={register}
                      name="productId"
                      placeholder={<FormattedMessage id="Select Course" />}
                      defaultValue={selectProduct}
                      // className={classnames(
                      //   "react-select",
                      //   !changeSelect
                      //     ? {
                      //         "is-invalid": errors["productId"],
                      //       }
                      //     : ""
                      // )}
                      options={product?.map((item, index) => {
                        return {
                          value: item?.id,
                          label: item.product_names[0].name || "",
                        };
                      })}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        setValue("productId", e);
                        setSelectProduct(e);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}

            <small className="text-danger">
              {errors?.productId && errors.productId.message}
            </small>
          </FormGroup>
          <FormGroup>
            <Label for="status">
              <FormattedMessage id="Status" />
            </Label>

            <Input
              type="select"
              name="status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              id="status"
              innerRef={register({ required: true })}
            >
              <option value="1">{intl.formatMessage({ id: "Active" })}</option>
              <option value="0">{intl.formatMessage({ id: "Blocked" })}</option>
            </Input>
          </FormGroup>
          <FormGroup>
            <Label for="Image">
              <FormattedMessage id="Image" />
            </Label>
            <Row className="align-items-end p-1">
              <Media className="mr-25" left>
                <Media
                  object
                  className="rounded mr-50 objectFit-contain"
                  src={avatar}
                  height="100"
                  width="100"
                />
              </Media>{" "}
              <Row className="flex-column px-1">
                <Button.Ripple tag={Label} className="mt-1" color="primary">
                  <FormattedMessage id="Upload" />
                  <Input
                    type="file"
                    onChange={(e) => onChange(e)}
                    hidden
                    accept="image/*"
                  />
                </Button.Ripple>
              </Row>
            </Row>
          </FormGroup>
          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CertificateTab);
