export const getCertificate = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_CERTIFICATE.method,
      path: api.GET_CERTIFICATE.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_CERTIFICATE",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};

export const addCertificate = (Certificate) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_CERTIFICATE.method,
      path: api.CREATE_CERTIFICATE.path,
      data: Certificate,
    })
      .then((response) => {
        dispatch({
          type: "ADD_CERTIFICATE",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_CERTIFICATE",
          err,
        })
      );
  };
};
export const updateCertificate = (Certificate) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_CERTIFICATE.method,
      path: api.UPDATE_CERTIFICATE.path,
      data: Certificate,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_CERTIFICATE",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_CERTIFICATE",
          err,
        })
      );
  };
};
